/* ============================================
   RPG Panel - 全局样式
   ============================================ */

@import url('https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;600;700&family=Outfit:wght@400;500;600;700&family=Noto+Sans+SC:wght@300;400;500;700&display=swap');
@import './theme.css';

/* === 应用容器 === */
#rpg-panel-app {
  font-family: var(--font-primary);
  font-size: 14px;
  line-height: 1.5;
  color: var(--color-text-primary);
  background: var(--gradient-dark);
  min-height: 100vh;
  overflow-x: hidden;
}

/* === 玻璃卡片基础类 === */
.glass-panel {
  background: var(--glass-bg);
  backdrop-filter: var(--glass-blur);
  -webkit-backdrop-filter: var(--glass-blur);
  border: var(--border-subtle);
  border-radius: var(--border-radius-lg);
  box-shadow: var(--glass-shadow);
}

.glass-panel--elevated {
  background: rgba(30, 33, 50, 0.85);
  border: 1px solid rgba(255, 255, 255, 0.1);
}

/* === 按钮基础样式 === */
.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  padding: 10px 20px;
  font-family: var(--font-primary);
  font-size: 14px;
  font-weight: 500;
  color: var(--color-text-primary);
  background: var(--glass-bg);
  border: var(--border-subtle);
  border-radius: var(--border-radius-md);
  cursor: pointer;
  transition: all var(--transition-normal);
  outline: none;
  position: relative;
  overflow: hidden;
}

.btn::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255,255,255,0.1), transparent);
  transition: left 0.5s ease;
}

.btn:hover::before {
  left: 100%;
}

.btn:hover {
  border-color: var(--color-accent-primary);
  box-shadow: var(--shadow-glow-purple);
  transform: translateY(-2px);
}

.btn:active {
  transform: translateY(0);
}

.btn--primary {
  background: var(--gradient-primary);
  border: none;
}

.btn--primary:hover {
  box-shadow: var(--shadow-glow-purple), var(--shadow-md);
}

.btn--gold {
  background: var(--gradient-gold);
  border: none;
  color: hsl(230, 25%, 10%);
}

.btn--gold:hover {
  box-shadow: var(--shadow-glow-gold), var(--shadow-md);
}

.btn--ghost {
  background: transparent;
  border: 1px solid var(--color-text-muted);
}

.btn--ghost:hover {
  background: rgba(255, 255, 255, 0.05);
  border-color: var(--color-text-secondary);
}

/* === 输入框基础样式 === */
.input {
  width: 100%;
  padding: 12px 16px;
  font-family: var(--font-primary);
  font-size: 14px;
  color: var(--color-text-primary);
  background: rgba(0, 0, 0, 0.3);
  border: var(--border-subtle);
  border-radius: var(--border-radius-md);
  outline: none;
  transition: all var(--transition-normal);
}

.input:focus {
  border-color: var(--color-accent-primary);
  box-shadow: 0 0 0 3px rgba(155, 89, 255, 0.15);
}

.input::placeholder {
  color: var(--color-text-muted);
}

/* === 标题样式 === */
.title {
  font-family: var(--font-display);
  font-weight: 600;
  color: var(--color-text-primary);
  letter-spacing: 0.02em;
}

.title--xl {
  font-size: 28px;
}

.title--lg {
  font-size: 22px;
}

.title--md {
  font-size: 18px;
}

.title--sm {
  font-size: 15px;
}

.title--gradient {
  background: var(--gradient-gold);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

/* === 分隔线 === */
.divider {
  height: 1px;
  background: linear-gradient(90deg, transparent, rgba(255,255,255,0.1), transparent);
  margin: 16px 0;
}

/* === 徽章 === */
.badge {
  display: inline-flex;
  align-items: center;
  padding: 4px 10px;
  font-size: 12px;
  font-weight: 500;
  border-radius: 20px;
  background: rgba(255, 255, 255, 0.1);
}

.badge--primary {
  background: var(--color-accent-primary);
}

.badge--gold {
  background: var(--color-accent-secondary);
  color: hsl(230, 25%, 10%);
}

.badge--success {
  background: var(--color-success);
}

.badge--danger {
  background: var(--color-danger);
}

/* === 动画关键帧 === */
@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}

@keyframes slideUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes slideDown {
  from {
    opacity: 0;
    transform: translateY(-20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes scaleIn {
  from {
    opacity: 0;
    transform: scale(0.95);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}

@keyframes pulse {
  0%, 100% { opacity: 1; }
  50% { opacity: 0.6; }
}

@keyframes shimmer {
  0% { background-position: -200% 0; }
  100% { background-position: 200% 0; }
}

@keyframes glow {
  0%, 100% { box-shadow: var(--shadow-glow-purple); }
  50% { box-shadow: 0 0 30px rgba(155, 89, 255, 0.5); }
}

/* === 工具类 === */
.animate-fadeIn { animation: fadeIn var(--transition-normal) ease forwards; }
.animate-slideUp { animation: slideUp var(--transition-normal) ease forwards; }
.animate-slideDown { animation: slideDown var(--transition-normal) ease forwards; }
.animate-scaleIn { animation: scaleIn var(--transition-normal) ease forwards; }
.animate-pulse { animation: pulse 2s ease-in-out infinite; }
.animate-glow { animation: glow 2s ease-in-out infinite; }

.text-center { text-align: center; }
.text-left { text-align: left; }
.text-right { text-align: right; }

.flex { display: flex; }
.flex-col { flex-direction: column; }
.items-center { align-items: center; }
.justify-center { justify-content: center; }
.justify-between { justify-content: space-between; }
.gap-sm { gap: 8px; }
.gap-md { gap: 16px; }
.gap-lg { gap: 24px; }
